import React, { useState } from 'react';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import rock from './Img/rock.jpg';
import logo from './Img/logo.jpg';
import paper from './Img/paper.jpg';
import scissor from './Img/scissor.jpg';
//parseInt(Math.random() * (3-0) + 0);

let cr = 0;
let cs = 0;
let cp = 0;
let ur = 10;
let us = 20;
let up = 30;

const resetFun = () => {
    cr = 0;
    cs = 0;
    cp = 0;
    ur = 10;
    us = 20;
    up = 30;
}
const Heading = () => {
    const [pic, setPic] = useState(logo);
    let [cPoint, setCPoint] = useState(0);
    let [uPoint, setUPoint] = useState(0);
    // onClickFun Starts
    const onClickFun = (e) => {
        const randVal = parseInt(Math.random() * (3 - 0) + 0);
        console.log(randVal + 1 + '::' + e.target.id + ' clicked');
        const userClicked = e.target.id;
        const clickedFun = () => {
            if (userClicked === 'r') {
                ur = 100;
                up = 300;
            }
            else if (userClicked === 's') {
                us = 200;
                ur = 100;
            }
            else if (userClicked === 'p') {
                up = 300;
                us = 200;
            }
        }
        setPic(() => {
            if (randVal === 0) {
                cr = 1;
                return rock;

            }
            else if (randVal === 1) {
                cs = 1;
                return scissor;
            }
            else {
                cp = 1;
                return paper;
            }
        });
        const setPoinFun = () => {
            if ((cr === 1 && us === 200) && (cr === 1 && ur === 100) && (cr === 1 && up === 30)) {
                setCPoint(cPoint = cPoint + 1);
            }
            else if ((cr === 1 && up === 300) && (cr === 1 && us === 200) && (cr === 1 && ur === 10)) {
                setUPoint(uPoint = uPoint + 1);
            }
            else if ((cr === 1 && ur === 100) && (cr === 1 && up === 300) && (cr === 1 && us === 20)) {
                setUPoint(uPoint = uPoint);
            }
            else if ((cs === 1 && ur === 100) && (cs === 1 && up === 300) && (cs === 1 && us === 20)) {
                setUPoint(uPoint = uPoint + 1);
            }
            else if ((cs === 1 && up === 300) && (cs === 1 && us === 200) && (cs === 1 && ur === 10)) {
                setCPoint(cPoint = cPoint + 1);
            }
            else if ((cs === 1 && us === 200) && (cs === 1 && ur === 100) && (cs === 1 && up === 30)) {
                setCPoint(cPoint = cPoint);
            }
            else if ((cp === 1 && ur === 100) && (cp === 1 && up === 300) && (cp === 1 && us === 20)) {
                setCPoint(cPoint = cPoint + 1);
            }
            else if ((cp === 1 && up === 300) && (cp === 1 && us === 200) && (cp === 1 && ur === 10)) {
                setCPoint(cPoint = cPoint);
            }
            else {
                setUPoint(uPoint = uPoint + 1);
            }
            // else if ((cp === 1 && us === 200) && (cp === 1 && ur === 100)) {
            //     setUPoint(uPoint = uPoint + 1);
            // }
        }
        clickedFun();
        setPoinFun();
        resetFun();
    }
    return (<>
        <div className="row">
            <div className="col-3">&nbsp;&nbsp;</div>
            <div className="col">
                <div className="card border-3 w-100 shadow w-50 " style={{ width: '18rem' }}>
                    <img src={pic} style={{ height: '250px' }} className="card-img-top shadow" alt="My Logo" />
                    <div className="card-body">
                        <h5 className="text-center card-title"><b>ROCK PAPER SESSIOR</b></h5>
                        <p className="card-text text-capitalize">Select on of your choice and see wether you win or loose.</p>
                        <div className="text-center">
                            <button id='r' className='btn btn-outline-dark' onClick={onClickFun}>Rock</button>
                            <button id='s' className='btn btn-outline-danger' onClick={onClickFun}>Sessior</button>
                            <button id='p' className='btn btn-outline-success' onClick={onClickFun}>Paper</button>
                        </div>
                    </div>
                    <table className="table table-striped border">
                        <thead className="thead">
                            <th className="text-center">Computer</th>
                            <th>You</th>
                        </thead>
                        <tbody>
                            <tr>
                                <td><h2 className="text-center">{cPoint}</h2></td>
                                <td><h2>{uPoint}</h2></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div className="col-3">&nbsp;</div>
        </div>
    </>);
}
export default Heading;